import {ElMessageBox} from "element-plus";

export async function confirmDialog(message, title = '提示', type = 'warning') {
    try {
        const confirmResult = await ElMessageBox.confirm(message, title, {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: type,
        });
        return confirmResult === 'confirm'; // 返回 true 表示确认，false 表示取消
    } catch (error) {
        return false; // 捕获取消操作，返回 false
    }
}
