<template>
  <div>
    <el-button type="success"
               style="margin-bottom: 20px;"
               @click="fetchUsers">
      刷新
    </el-button>

    <el-button
        type="primary"
        @click="dialogVisible = true"
        style="margin-left: 20px;margin-bottom: 20px;"
    >
      新增配置
    </el-button>

    <el-input
        v-model="pageParams.keyword"
        placeholder="模糊搜索名称/区域"
        @input="fetchUsers"
        clearable prefix-icon="Search"
        style="margin-left: 20px; width: 200px; margin-bottom: 20px;"
    />
    <el-select
        v-model="pageParams.isEnableCreate"
        @change="fetchUsers"
        placeholder="开机任务状态筛选"
        style="margin-left: 20px; width: 180px; margin-bottom: 20px;"
    >
      <el-option label="全部" :value="null"></el-option>
      <el-option label="仅显示执行开机任务中" :value="1"></el-option>
      <el-option label="无开机任务" :value="0"></el-option>
    </el-select>

    <el-button
        type="danger"
        @click="showDeleteSelected"
        :disabled="!selectedUsers.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量删除
    </el-button>

    <el-button
        type="warning"
        @click="createBatch"
        :disabled="!selectedUsers.length"
        style="margin-left: 20px; margin-bottom: 20px;"
    >
      批量开机
    </el-button>

    <el-button
        type="success"
        @click="checkAlive"
        style="margin-left: 20px; margin-bottom: 20px;"
        :loading="checkLoading"
    >
      一键测活
    </el-button>

    <!-- 可滑动的表格 -->
    <div style="overflow-x: auto;">
      <el-table
          :data="users"
          style="width: 100%; min-width: 800px;"
          v-loading="loading"
          @selection-change="handleSelectionChange"
      >
        <el-table-column type="selection" width="55"/>
        <el-table-column label="名称（点击修改）">
          <template #default="{ row }">
            <el-tooltip :content="row.username" placement="top">
              <el-button @click="handleUpdateUsername(row)" type="text" link>{{ row.username }}</el-button>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="租户名">
          <template #default="{ row }">
            <el-tag type="success">
              {{ row.tenantName }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="区域">
          <template #default="{ row }">
            <el-tag type="primary">
              {{ row.region }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="开机任务状态">
          <template #default="{ row }">
            <el-tag :type="row.enableCreate === 1 ? 'success':'info'">
              {{ row.enableCreate === 1 ? '执行开机任务中' : '无开机任务' }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column sortable prop="createTime" label="创建时间">
          <template #default="{ row }">
            <el-tag type="info">
              {{ row.createTime }}
            </el-tag>
          </template>
        </el-table-column>
        <!-- 缩小的操作列 -->
        <el-table-column fixed="right" label="操作" width="300">
          <template #default="{ row }">
            <el-button @click="viewDetails(row)" type="primary" link>详情</el-button>
            <el-button @click="releaseSecurityRule(row)" type="success" link>安全列表放行</el-button>
            <el-button @click="createInstance(row)" type="warning" link>开机</el-button>
            <el-button @click="handleStopCreate(row)" link
                       :type="row.enableCreate === 0 ? 'info' : 'danger'"
                       :disabled="row.enableCreate === 0">停止开机
            </el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页控制 -->
    <el-pagination
        @current-change="handlePageChange"
        @size-change="handleSizeChange"
        v-model:current-page="pageParams.currentPage"
        v-model:page-size="pageParams.pageSize"
        :page-sizes="[5, 10, 20, 50]"
        :total="total"
        layout="total, sizes, prev, pager, next, jumper"
        style="margin-top: 20px;"
    />
  </div>
  <el-dialog
      v-model="dialogVisible"
      title="新增配置"
      width="50%"
      @close="dialogVisible = false"
  >
    <el-tabs v-model="activeTab">
      <el-tab-pane label="手动输入" name="manual">
        <el-form ref="formRef" :model="addCfgFormData" :rules="formRules" label-width="80px">
          <el-form-item label="配置名称" prop="username">
            <el-input v-model="addCfgFormData.username" placeholder="请输入自定义配置名称，例：这是我的首尔" clearable/>
          </el-form-item>
          <el-form-item label="配置内容" prop="ociCfgStr">
            <el-input
                v-model="addCfgFormData.ociCfgStr"
                type="textarea"
                :placeholder="placeholderText"
                rows="6"
            />
          </el-form-item>
          <el-form-item label="私钥文件" prop="file">
            <el-upload
                drag
                :http-request="handleSingleFileUpload"
                style="width: 100%;"
                :show-file-list="true"
                :file-list="singleFileList"
            >
              <el-icon>
                <Key/>
              </el-icon>
              <div>
                请上传私钥文件（xxx.pem） <em>点击选择文件或拖动文件到此处</em>
              </div>
            </el-upload>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="批量上传" name="upload">
        <el-upload
            class="upload-demo"
            drag
            :http-request="handleFileUpload"
            multiple
            :show-file-list="true"
            :file-list="fileList"
        >
          <el-icon class="el-icon--upload">
            <upload-filled/>
          </el-icon>
          <div class="el-upload__text">
            Drop file here or <em>click to upload</em>
          </div>
          <template #tip>
            <div class="el-upload__tip">
              ⭐要求：文件格式为.ini或者.txt，每个API配置的第一行是你[自定义的配置名称]，例：[首尔01]。
              私钥文件（.pem）需要先上传至/app/oci-helper/keys目录下，然后修改配置中的key_file=私钥文件名称.pem。
              上传过程中会校验配置有效性，如不符合要求则无法添加成功~
            </div>
          </template>
        </el-upload>
      </el-tab-pane>
    </el-tabs>
    <template #footer>
      <el-button @click="dialogVisible = false">取消</el-button>
      <el-button type="primary" @click="activeTab === 'manual' ? addUser() : uploadFile()"
                 :disabled="activeTab === 'upload' && this.fileList.length !== 0 ? false : !addCfgFormData.disabled"
                 :loading="addLoading">
        {{ activeTab === 'manual' ? '新增' : '上传配置文件' }}
      </el-button>
    </template>
  </el-dialog>
  <el-dialog
      v-model="checkDialogVisible"
      title="测活结果"
      width="35%"
      @close="checkDialogVisible = false"
  >
    <span>{{ this.checkResult }}</span>
    <template #footer>
      <div class="dialog-footer">
        <el-button type="primary" @click="checkDialogVisible = false">
          确定
        </el-button>
      </div>
    </template>
  </el-dialog>

  <el-dialog
      v-model="updateCfgDialog"
      title="修改配置名称"
      width="30%"
      @close="updateCfgDialog = false"
  >
    <el-input v-model="updateCfgParams.updateCfgName" placeholder="请输入自定义配置名称，例：这是我的首尔" clearable/>
    <template #footer>
      <div class="dialog-footer">
        <el-button type="info" @click="updateCfgDialog = false">
          取消
        </el-button>
        <el-button type="primary" @click="updateOciCfgName()" :loading="updateCfgLoading">
          修改
        </el-button>
      </div>
    </template>
  </el-dialog>
</template>

<script>
import {ElTable, ElTableColumn, ElPagination, ElInput, ElButton, ElMessage, ElMessageBox} from 'element-plus';
import {UploadFilled} from '@element-plus/icons-vue'
import {confirmDialog} from '@/js/common.js'

export default {

  components: {
    ElTable, ElTableColumn, ElPagination, ElInput, ElButton, UploadFilled
  },
  data() {
    return {
      updateCfgParams: {
        cfgId: '',
        updateCfgName: '',
      },
      updateCfgDialog: false,
      updateCfgLoading: false,
      checkLoading: false,
      checkResult: '',
      users: [],
      pageParams: {
        keyword: '',
        currentPage: 1,
        pageSize: 10,
        isEnableCreate: null
      },
      total: 2,
      selectedUsers: [],
      loading: true,
      addLoading: false,
      dialogVisible: false,
      checkDialogVisible: false,
      activeTab: "manual", // 用于切换选项卡
      fileList: [],
      singleFileList: [],
      addCfgFormData: {
        disabled: false,
        username: '',
        ociCfgStr: '',
        file: null
      },
      formRules: {
        username: [
          {required: true, message: '配置名称不能为空', trigger: 'blur'},
          {min: 1, message: '配置名称至少为 1 个字符', trigger: 'blur'},
        ],
        ociCfgStr: [
          {required: true, message: '配置内容不能为空', trigger: 'blur'},
          {min: 10, message: '配置内容至少为 10 个字符', trigger: 'blur'},
        ],
        file: [
          {required: true, message: '私钥文件不能为空', trigger: 'change'},
        ],
      },
      placeholderText: "user=ocid1.user.oc1..aaaaaaaaxxx\nfingerprint=c6:1b:9f:cd:01:9d:7a:xxx\ntenancy=ocid1.tenancy.oc1..aaaaaaaaxxx\nregion=sa-saopaulo-1\n"
    };
  },
  watch: {
    // 监听表单数据变化，触发验证以更新按钮状态
    addCfgFormData: {
      handler() {
        this.validateForm();
      },
      deep: true,
    },
  },
  methods: {
    async fetchUsers() {
      this.loading = true;
      this.$axios.post('/oci/userPage', {...this.pageParams})
          .then(response => {
            this.users = response.data.data.records;
            this.total = response.data.data.total;
          })
          .catch(error => {
            console.error('Error:', error)
          })
      this.loading = false;
    },
    async checkAlive() {
      this.checkLoading = true
      await this.$axios.post('/oci/checkAlive', {})
          .then(response => {
            this.checkLoading = false
            if (response.data.success) {
              this.checkDialogVisible = true
              this.checkResult = response.data.msg
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    async updateOciCfgName() {
      const confirmed = await confirmDialog('确定要修改配置名称吗？', '修改配置名称');
      if (!confirmed) {
        this.$message.info('已取消');
        return;
      }

      this.updateCfgLoading = true
      await this.$axios.post('/oci/updateCfgName', {...this.updateCfgParams})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: '修改配置名称成功',
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.updateCfgLoading = false
            this.updateCfgDialog = false
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    handleUpdateUsername(row) {
      this.updateCfgDialog = true
      this.updateCfgParams.cfgId = row.id
      this.updateCfgParams.updateCfgName = row.username
    },
    handlePageChange(page) {
      this.pageParams.currentPage = page;
      this.fetchUsers();
    },
    handleSizeChange(size) {
      this.pageParams.currentPage = 1;
      this.pageParams.pageSize = size;
      this.fetchUsers();
    },
    handleSelectionChange(val) {
      this.selectedUsers = val;
    },
    handleSingleFileUpload({file}) {
      this.singleFileList = [file]
      this.addCfgFormData.file = file;
    },
    // 自定义文件上传逻辑
    handleFileUpload({file}) {
      const reader = new FileReader();
      reader.onload = (e) => {
        const arrayBuffer = e.target.result;
        if (arrayBuffer) {
          this.fileList.push(file);
        }
      };
      reader.readAsArrayBuffer(file); // 直接使用 file
    },
    async uploadFile() {
      this.addLoading = true
      // 构造 FormData 对象
      const formData = new FormData();
      this.fileList.forEach((file) => {
        formData.append("fileList", file); // 后端参数名必须与 @NotEmpty 的变量名一致
      });
      // 发起 POST 请求
      await this.$axios.post("/oci/uploadCfg", formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      }).then(response => {
        if (response.data.success) {
          this.resetForm()
          ElMessage({
            message: response.data.msg,
            type: 'success',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        } else {
          this.addLoading = false
          ElMessage({
            message: response.data.msg,
            type: 'error',
            duration: 2000 // 设置提示显示时间，单位是毫秒
          });
        }
        this.fetchUsers();
      }).catch(error => {
        console.error('Error:', error)
      })
      this.resetForm()
    },
    // 重置表单和状态
    resetForm() {
      this.addCfgFormData = {
        disabled: false,
        username: '',
        ociCfgStr: '',
      }
      this.fileList = []
      this.addLoading = false
      this.dialogVisible = false
    },
    async addUser() {
      if (this.singleFileList.length === 0) {
        ElMessage({
          message: "请先上传私钥文件",
          type: "warning",
          duration: 2000
        });
        return;
      }

      const formData = new FormData();
      formData.append("username", this.addCfgFormData.username);
      formData.append("ociCfgStr", this.addCfgFormData.ociCfgStr);
      formData.append("file", this.addCfgFormData.file);  // 取 `raw` 才是文件

      this.addLoading = true
      await this.$axios.post('/oci/addCfg', formData, {
        headers: {"Content-Type": "multipart/form-data"}
      })
          .then(response => {
            if (response.data.success) {
              this.resetForm()
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              this.addLoading = false
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    viewDetails(row) {
      this.$router.push({
        path: '/dashboard/OciCfgTab',
        query: {cfgId: row.id, username: row.username},
      });
    },
    createInstance(row) {
      this.$router.push({
        path: '/dashboard/createInstance',
        query: {row: JSON.stringify(row)},
      });
    },
    createBatch() {
      this.$router.push({
        path: '/dashboard/ociCreateInstanceBatch',
        query: {row: JSON.stringify(this.selectedUsers)},
      });
    },
    async releaseSecurityRule(row) {
      try {
        // 弹框确认
        const confirmResult = await ElMessageBox.confirm(
            '确定要放行默认 VCN 安全列表的所有端口及协议吗？', // 弹框内容
            '安全列表放行操作', // 弹框标题
            {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning', // 弹框类型
            }
        );

        // 如果用户点击了 "确定"
        if (confirmResult === 'confirm') {
          await this.$axios.post('/oci/releaseSecurityRule', {ociCfgId: row.id})
              .then(response => {
                if (response.data.success) {
                  ElMessage({
                    message: response.data.msg,
                    type: 'success',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });

                } else {
                  ElMessage({
                    message: response.data.msg,
                    type: 'error',
                    duration: 2000 // 设置提示显示时间，单位是毫秒
                  });
                }
                this.fetchUsers();
              })
              .catch(error => {
                console.error('Error:', error)
              })
        }
      } catch (cancelError) {
        // 用户点击了 "取消" 或关闭了弹框，不需要执行任何操作
        ElMessage({
          message: '操作已取消',
          type: 'info',
          duration: 2000,
        });
      }
    },
    handleStopCreate(row) {
      const confirmed = window.confirm('确定要停止开机任务吗?');
      if (confirmed) {
        this.stopCreate(row);
      } else {
        this.$message.info('已取消');
      }
    },
    stopCreate(row) {
      this.$axios.post('/oci/stopCreate', {userId: row.id})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    showDeleteSelected() {
      const confirmed = window.confirm('确定要删除这些配置吗?');
      if (confirmed) {
        this.deleteSelected();
      } else {
        this.$message.info('取消删除');
      }
    },
    deleteSelected() {
      const ids = this.selectedUsers.map(user => user.id);
      this.$axios.post('/oci/removeCfg', {idList: ids})
          .then(response => {
            if (response.data.success) {
              ElMessage({
                message: response.data.msg,
                type: 'success',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            } else {
              ElMessage({
                message: response.data.msg,
                type: 'error',
                duration: 2000 // 设置提示显示时间，单位是毫秒
              });
            }
            this.fetchUsers();
          })
          .catch(error => {
            console.error('Error:', error)
          })
    },
    validateForm() {
      this.$refs.formRef.validate((valid) => {
        this.addCfgFormData.disabled = valid;
      });
    },
  }
  ,
  mounted() {
    this.fetchUsers();
  }
  ,
}
;
</script>

<style scoped>
.upload-demo {
  border: 2px dashed #d9d9d9;
  border-radius: 6px;
  padding: 20px;
  text-align: center;
  cursor: pointer;
}

.el-upload__text {
  font-size: 14px;
  color: #606266;
}

.el-upload__tip {
  margin-top: 10px;
  font-size: 12px;
  color: #909399;
}
</style>
